function getRandomInt(min,max){
  return Math.floor(Math.random(0,1)*(max-min+1)+min)
}
export function shuffle(array){
  let arr = array.slice()
  for(let i = 0 ; i<arr.length; i++){
    let j = getRandomInt(0,i-1)
    let t = arr[i]
    arr[i] = arr[j]
    arr[j] = t
  }
  return arr
}


